#sectionThumb {
  display: flex;
  margin: 56px 50px;
  gap: 50px 60px;
  flex-wrap: wrap;
}

.backThumb {
  width: 340px;
  height: 340px;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.imgThumb {
  width: 340px;
  height: 340px;
  object-fit: cover;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.titleThumb {
  position: absolute;
  left: 5.88%;
  bottom: 5.88%;
  margin: 0;
  font-size: 18px;
  line-height: 142.6%;
  display: flex;
  align-items: flex-end;
  color: #ffffff;
}
.thumblink {
  margin: 0 auto;
}

@media screen and (max-width: 768px) {
  #sectionThumb {
    margin: 0;
    gap: 20px 20px;
  }
  #sectionThumb a {
    margin: 0 auto;
  }
  .backThumb {
    width: 100%;
    height: 100%;
    min-height: 255px;
  }
  .imgThumb {
    width: 100%;
    height: 100%;
  }
}
